#services {
  background: #eeece6 url("../img/02.jpg") no-repeat top left /cover;
  position: relative;
}

#services h2 {
  font-size: 5vw;
  text-shadow: 0 0 5px white;
  text-transform: uppercase;
}

.services__wrapper {
  position: absolute;
  top: 100px;
  right: 10%;
  width: 50%;
}

.services__list {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: baseline;
}

.services__more {
  text-align: center;
  margin: 10px 0;
}

.services__item {
  width: 30%;
  min-width: 220px;
  list-style: none;
  text-align: center;
  margin-top: 2vh;
  padding: 10px;
  color: #fff;
  position: relative;
  background-color: rgba(#fff, 0.2);

  &:hover,
  &:focus,
  &:active {
    box-shadow: 0 0 8px 3px rgba(#000, 0.5);
    background-color: rgba(#fff, 0.6);
  }
}

.services__title {
  font-size: 1em;
  text-transform: uppercase;
  text-align: center;
  margin-top: -12%;
  color: #fff;
  text-shadow: 0 0 3px #000;
}

@media (max-width: 1300px) {
  .services__wrapper {
    width: 68%;
  }
}

@media (max-width: 992px) {
  .services__wrapper {
    position: static;
    width: 100%;
    padding: 100px 20px 50px;
  }
  .services__title {
    font-size: 18px;
  }
}
