#team {
  background: #eeece6 url("../img/04.jpg") no-repeat top right /cover;
}

.team__list {
  margin: 0;
  padding-top: 150px;
  width: 50%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: flex-start;
  flex-direction: column;
}

.team__item {
  height: 25vh;
  padding: 15px;
  list-style: none;
  position: relative;
  color: #fff;
  margin-bottom: 2vh;
  display: flex;
  align-items: center;
}

.team__image {
  max-width: 100%;
  width: auto;
  max-height: 100%;
  margin-right: 30px;
  display: block;
  z-index: 0;
}

.team__header {
  text-align: center;
  font-size: 3vh;
}

.team__text {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 70vw;
  height: auto;
  min-height: 26vh;
  opacity: 0;
  padding: 5%;
  background: #000;
  text-align: center;
  font-size: 2vh;
  z-index: -1;
  display: block;
}

.team__item {
  &:hover > .team__text,
  &:focus > .team__text {
    opacity: 1;
    transition: 1s;
    z-index: 101;
  }
}

@media (max-width: 992px) {
  #team {
    background-position: left;
  }

  .team__item {
    height: 200px;
  }

  .team__text {
    font-size: 14px;
    min-height: 200px;
  }
}

@media (max-width: 700px) {
  .team__list {
    width: auto;
    padding: 50px 20px;
  }

  .team__item {
    flex-direction: column;
    margin-bottom: 30px;
    width: 100%;
  }

  .team__header {
    font-size: 18px;
  }

  .team__text {
    width: 100%;
  }
}
