.popap-menu {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    padding: 125px 25% 20px;
    background: rgba(#000, 0.6);
    align-items: center;
    width: 100vw;
    max-width: 100%;
    visibility: hidden;
    z-index: -1;
    justify-content: center;
}

.popap-menu__item {
  padding: 10px 20px;
  list-style: none;
}

.popap-menu__link {
  color: #fff;
  font-size: 16px;
  text-transform: uppercase;

  &:hover,
  &:focus,
  &:active {
    color: $hover-color;
  }
}

@media (max-width: 992px) {
  .popap-menu {
    position: static;
    visibility: visible;
    display: block;
    padding-top: 20px;
    display: none;
  }

  .popap-menu__item {
    border-bottom: 1px solid #fff;
    width: 100%;
  }
}
