#gallery {
  background: #eeece6 url("../img/03.jpg") no-repeat left center /cover;
  overflow: hidden;
  position: relative;
}

.slider__gallery {
  position: absolute;
  top: 25vh;
  left: 50%;
  margin-left: -40vw;
  bottom: auto;
  width: 80vw !important;
  max-height: 65vh;
  overflow: hidden;

  & img {
    max-height: 65vh;
    width: auto;
  }
}

#gallery .owl-theme .owl-dots .owl-dot {
  outline: none;
}

#gallery .owl-theme .owl-dots .owl-dot span {
  width: 1vw;
  height: 1vw;
  background: #ce9258;
}

#gallery .owl-theme .owl-dots .owl-dot.active span,
#gallery .owl-theme .owl-dots .owl-dot:hover span,
#gallery .owl-theme .owl-dots .owl-dot:focus span,
#gallery .owl-theme .owl-dots .owl-dot:active span {
  background: #8f470c;
}

#gallery .owl-prev,
#gallery .owl-next {
  position: absolute;
  top: 50%;
  margin-top: -60px;
  font-size: 84px;
  color: #fff;
  font-weight: bold;
}

#gallery .owl-nav .owl-prev:focus,
#gallery .owl-nav .owl-prev:focus,
#gallery .owl-nav .owl-prev:active,
#gallery .owl-nav .owl-next:focus,
#gallery .owl-nav .owl-next:focus,
#gallery .owl-nav .owl-next:active,
#gallery .owl-theme .owl-nav [class*="owl-"]:hover {
  background: transparent;
  color: #8f470c;
  outline: none;
}


#gallery .owl-prev {
  left: 2%;
}

#gallery .owl-next {
  right: 2%;
}

@media (max-width: 992px) {
  #gallery {
    padding: 50px 20px;
  }
  .slider__gallery {
    position: static !important;;
    width: auto !important;
    margin-left: 0;
  }
}

/*Gallery page*/

.other-page #gallery {
  background: none;
  margin-bottom: 50px;
  display: flex;
  flex-wrap: wrap;
}

.other-page #gallery:after {
  content: '';
  display: block;
  height: 0;
  clear: both;
}

.other-page .container {
  width: 100%;
  max-width: 960px;
  margin: 50px auto;
}

.other-page .photo {
  width: 33.333333%;
  padding: 10px;
  box-sizing: border-box;
  position: relative;
  margin: 0;

  & img {
    display: inline-block;
    max-width: 100%;
    height: auto;
    border: 10px solid #eee;
  }
}

.other-page .photo a:hover {
    opacity: 0.5;
}

.other-page .photo a {
    display: block;
}

.other-page .photo a:after {
    content: '';
    background: transparent url("../img/eye-icon.png") no-repeat center;
    width: 52px;
    height: 35px;
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    visibility: hidden;
}

.other-page .photo:hover > a:after {
    visibility: visible;
}

.other-page .photo figcaption {
    color: #999999;
    text-align: center;
    margin-top: 20px;
}

@media (max-width: 992px) {
  .other-page .photo {
    width: 50%;
  }
}

@media (max-width: 450px) {
  .other-page .photo {
    width: 100%;
  }
}
