.button {
  display: inline-block;
  padding: 10px 40px;
  background-color: #231818;
  border-radius: 10px;
  color: #fff;

  &:hover,
  &:focus {
    background-color: $hover-color;
  }

  &:active {
    background-color: $hover-color;
    box-shadow: 0 0 6px 3px inset rgba(#000, 0.4);
  }

  &--gallery {
    position: absolute;
    right: 10vw;
    bottom: 3vh;
  }
}

@media (max-width: 992px) {
  .button {
    &--gallery {
      right: 20px;
      bottom: 20px;
    }
  }
}
