.header {
  background-color: #000;
  width: 100vw;
  max-width: 100%;
  padding: 20px;
  z-index: 101;
  position: fixed;
  top: 0;

  &--front {
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(#000, 0.5);
  }
}

.header__navigation {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  width: 80vw;
  margin: 0 auto;

  &--nojs .popap-menu {
    visibility: visible;
  }
}

.header__logo {
  max-width: 15%;
}

@media (max-width: 992px) {
  .header,
  .header--front {
    position: static;
    background-color: #000;
    margin: 0;
    width: 100%;
  }

  .header__logo {
    max-width: 30%;
  }
}

@media (max-width: 700px) {
  .header__navigation {
    width: 90vw;
  }

  .header__logo {
    max-width: 40%;
  }
}
