#contacts {
  background: #eeece6 url("../img/05.jpg") no-repeat center right /cover;
  text-align: center;
  font-size: 2vw;
  position: relative;
}

.contacts_wrapper {
  position: absolute;
  top: 150px;
  left: 10%;
  width: 50%;
  color: #000;
}

@media (max-width: 1200px) {
  .contacts_wrapper {
    left: 0;
  }
}

.contacts_wrapper a {
  color: #000;
}

#contacts h2 {
  font-size: 5vw;
  font-weight: bold;
  text-transform: uppercase;
}

.messangers {
  display: flex;
  justify-content: space-between;
  width: 150px;
  margin: 10px auto;
}

.messangers_item {
  width: 40px;
  height: 40px;
}

@media (max-width: 992px) {
  #contacts {
    font-size: 20px;
  }

  .contacts_wrapper {
    position: static;
    width: 100%;
    padding: 50px 20px;
    text-align: left;
  }
}

@media (max-width: 700px) {
  #contacts,
  .contacts_wrapper {
    background-position: left;
    text-align: center;
  }

  #contacts h2 {
    font-size: 32px;
  }
}
