.about {
  background: grey url("../img/IMG_2215.jpg") no-repeat top right /cover;
  text-align: left;
  position: relative;
}

.about__title {
  position: absolute;
  top: 20vh;
  left: 30%;
  margin-left: -22vw;
}

.about__header {
  font-size: 10vw;
  color: #e11a2d;
  text-shadow: 3px 3px 3px #1e1d19;
  font-family: "Times New Roman";
  text-transform: uppercase;
  width: 58vw;

  &--left {
    color: #994136;
  }

  &--right {
    color: #eac5b0;
    padding-left: 15%;
  }
}

.about__slogan {
  font-size: 2vw;
  color: #e8e9f0;
  text-shadow: 0 0 5px #686363;
  width: 17vw;
  position: absolute;
  top: 20%;
  right: 5vw;
  border-left: 3px solid;
  padding: 0 20px;
  text-transform: uppercase;
}

.about__text {
  position: fixed;
  top: 50%;
  margin-top: -49vh;
  left: 50%;
  margin-left: -49vw;
  width: 98vw;
  height: 98vh;
  background-color: rgba(#fff, 0.9);
  padding: 11vh 20px 20px;
  z-index: 101;
  border-radius: 25px;
  border: 3px solid #000;
  display: none;
  font-size: 1.8vh;
}

.about__button {
  display: inline-block;
  width: 80px;
  height: 80px;
  line-height: 1.2;
  border-radius: 50%;
  font-size: 20px;
  color: rgba(#000, 0.5);
  background-color: rgba(#fff, 0.3);
  position: absolute;
  top: 30%;
  left: 30px;
  text-align: center;
  padding: 25px 0;

  &:hover,
  &:focus {
    animation: shake 0.6s;
    background-color: #fff;
    color: $hover-color;
  }

  &--open {
    width: 98vw;
    position: fixed;
    margin-left: -49vw;
    top: 0;
    padding: 20px;
    left: 50%;
    margin-left: -49vw;
    font-size: 4vh;
    z-index: 102;
    padding: 18px;
    background-color: #e9ccd0;
    border-radius: 0;
    height: 10vh;
    color: #000;
    border-radius: 25px 25px 0 0;
    border: 3px solid #000;

    &:hover,
    &:focus {
      animation: none;
      background-color: #e9ccd0;
      color: #000;
    }

    &::after {
      content: "x";
      position: absolute;
      right: 20px;
      top: 50%;
      margin-top: -10px;
      display: inline-block;
      font-size: 13px;
      color: #000;
      width: 20px;
      height: 20px;
      border: 2px solid #000;
      border-radius: 50%;
      font-weight: bold;
    }

    &:hover::after,
    &:focus::after,
    &:active::after {
      color: #fff;
      background-color: #000;
    }

    & + .about__text {
      display: block;
    }
  }

  &--nojs {
    cursor: default;

    &:active + .about__text {
      display: block;
    }
  }
}

@media (max-width: 992px) {
  .about__title {
    margin-left: 0;
    top: 20vh;
    left: 5%;
  }

  .about {
    height: 90vh;
  }

  .about__header {
    font-size: 10vh;
    width: 56vw;
  }

  .about__slogan {
    right: 10vw;
    top: 17%;
  }

  .about__button {
    top: 20px;

    &--open {
      top: 0;
    }
  }

  .about__text {
    font-size: 14px;
    overflow-y: scroll;

    & p {
      margin-top: 0;
    }
  }
}

@media (max-width: 700px) {
  .about__header {
    width: 100%;
    font-size: 42px;
    position: static;

    &--right {
      display: block;
      padding-left: 10%;
    }
  }

  .about__slogan {
    font-size: 24px;
    width: 210px;
    position: static;
    border-left: none;
    border-top: 3px solid;
    padding: 20px 0;
  }
}
